<template>
  <div class="px-15">
        <div class="subpage-title space-between">
            <a href="#" class="back-link">
                <i class="fa fa-angle-left"></i>
                Услуги
            </a>

            <a href="#" class="btn-sm" @click.prevent="createNew" v-if="!hasSelected">
                <i class="fa fa-plus"></i>
            </a>

            <a href="#" class="btn-sm" @click.prevent="createNew" v-else>
                <i class="fa fa-trash"></i>
            </a>
        </div>
        
        <service-edit 
            v-if="state.service" 
            :service="state.service"
            @update="handleSubmit"
            @cancel="state.service = null"
        />

        <service-list 
            :services="services"
            @select="state.service = $event"
            v-else
        />
    <!-- <div v-for="(service, ind) in services" :key="ind">
      <template v-if="editingIds[service.id]">

      </template>
      <template v-else>
        [ {{ service.status }} ] {{ service.name }} {{ service.price }} - {{ service.duration }} мин. <a href='#' @click.prevent="edit(service.id)">Edit</a>
      </template>
    </div> -->
  </div>
</template>

<script>
import { mapState } from "vuex";
import Service from "../models/Service";


import "./Services/index";

export default {
    data() {
        return {
            state: {
                service: null
            }
        }
    },
    mounted() {
        this.$store.dispatch('services/load');
    },
    computed: {
        ...mapState('services', {
            services: state => state.list
        }),
        hasSelected() {
            return this.state.service != null;
        }
    },
    methods: {
        createNew() {
            this.state.service = new Service();
        },
        handleSubmit(service) {
            this.$store.dispatch('services/create', service);
        },
        handleDelete(id) {
            this.$store.dispatch('services/deleteService', { id });
            this.cancel(id);
            this.$store.dispatch('services/load');
        },
        update(id) {
            this.$store.dispatch('services/update', {
                service: this.$store.getters['services/getById'](id)
            });
      
            this.cancel(id);
        }
  }
}
</script>
