<template>
    <div class="d-flex space-between">
        <template v-if="state.selected == null">
            <span>Клиент: Анонимен</span>
        </template>
        <template v-else>
            <span>Клиент: {{ state.selected.name }}</span>
        </template>
        <span>
            <a href="#customers" @click="setpick">Избери</a>            
            или <a href="#customers?new" @click="setpick">Създай нов</a>
        </span>
    </div>
</template>

<script>
export default {
    data() {
        return {
            filter: '',
            state: {
                selected: null
            }
        }
    },
    methods: {
        handleSelect(customer) {
            this.state.selected = customer;
            this.$emit('change', customer);
        },
        setpick() {
            this.$store.commit('dialog/pickCustomer');
        }
    }
}
</script>