<template>
    <div class="w-100 d-flex-col h-100 justify-content-center align-items-center">
        {{ dayOfTheWeek(th.getDay()) }} - {{ padStart(th.getDate(), 2, '0') }}.{{ padStart(th.getMonth() + 1, 2, '0') }}
    </div>
</template>

<script>

import padStart from "lodash/padStart";
import { dayOfTheWeek } from '../../../helpers/date';

export default {
    props: ['date'],
    computed: {
        th() {
            return new Date(this.date)
        }
    },
    methods: {
        dayOfTheWeek,
        padStart
    }
}
</script>

