import axios from "axios";

export function create(data) {
  return axios.post('/dashboard/services', data);
}

export function update(id, data) {
  return axios.patch(`/dashboard/services/${id}`, data);
}

export function list() {
  return axios.get('/dashboard/services/list');
}

export function deleteService(id) {
  return axios.delete(`/dashboard/services/${id}`);
}