<template>
    <form @submit="$emit('update', state.service)">
          <div class="d-flex mb-15">
                <div class="col">
                    <text-input v-model="state.service.name">
                        <template v-slot:label>Име</template>
                    </text-input>
                </div>
          </div>
          <div class="d-flex mb-15">
              <div class="col">
                    <text-input v-model="state.service.price">
                        <template v-slot:label>Цена</template>
                    </text-input>
              </div>
          </div>
          <div class="d-flex mb-15">
              <div class="col mr-15">
                    <input-select 
                        v-model="state.service.status" 
                        :suggestions="['available', 'unavailable', 'display-only', 'soon']"
                    >
                        <template v-slot:label>Статус</template>
                    </input-select>
              </div>
          </div>
          <div class="d-flex mb-15">
              <div class="col">
                    <div class="mb-15">Продължителност:</div>
                    <text-input v-model="state.service.duration">
                        <template v-slot:label>Минути</template>
                    </text-input>
              </div>
          </div>
          <div class="d-flex space-between">            
            <!-- <a 
                href="#"
                @click.prevent="handleDelete"
                v-if="state.service.id"
            >
                Изтрий
            </a> -->
            <a href="#" @click.prevent="cancel">Откажи</a>
            <network-button namespace="service">Запази</network-button>
          </div>
    </form>
</template>
<script>
import Service from "../../models/Service";

export default {
    props: {
        service: {
            type: Service
        }
    },
    data() {
        return {
            state: {
                service: this.service
            }
        }
    },
    methods: {
        cancel() {
            this.$emit('cancel');
        },
        handleDelete() {
            this.$emit('delete', this.state.service);
        }
    }
}
</script>