<template>
    <div class="px-15">
        <div class="subpage-title space-between">
          <a href="#" class="back-link">
                <i class="fa fa-angle-left"></i>
                Служители
            </a>
          <button @click.prevent="createNew" class="btn-sm" v-if="!hasSelected">
            <i class="fa fa-plus"></i>
          </button>
          <button @click.prevent="handleDelete" class="btn-sm btn-dissabled" v-else>
            <i class="fa fa-trash"></i>
          </button>
        </div>
        <template v-if="!hasSelected">
          <employee-list 
              :employees="list"
              @select="handleListSelect"
              @delete="handleDelete"
          />
        </template>

        <employee-edit
            v-else
            :employee="state.selectedEmployee"
            @save="handleSave"
            @cancel="handleListSelect(null)"
        />
    </div>
</template>

<script>

import "./Employees";
import { mapState } from "vuex";
import Employee from "../models/Employee";

export default {
    data() {
      return {
        state: {
          selectedEmployee: null
        }
      }
    },
    async created() {
      await Promise.all([
        this.$store.dispatch('employee/load'),
        this.$store.dispatch('services/load')
      ]);

      // this.checkHash();
    },
    computed: {
      ...mapState('employee', {
        list: 'list'
      }),
      hasSelected(){ return this.state.selectedEmployee != null }
    },
    methods: {
      handleListSelect(employee) {
        // if(employee) {
        //   const cleanHash = location.hash.replace('#', '');
        //   const hash = '#' + cleanHash.replace(/(\?[^=?]+=\d+)/, '') + `?id=${employee.id}`;
        //   location.hash = hash;
        // } else {
        //   location.hash = '#';
        // }

        this.state.selectedEmployee = employee;
      },
      // checkHash() {
      //   const res = location.hash.match(/\?id=(\d+)/);

      //   if(res && res[1]) {
      //     this.state.selectedEmployee = this.$store.getters['employee/getById'](res[1]);
      //   }
      // },
      handleDelete(employee) {
        // alert('Not available');
        this.$store.dispatch('employee.delete', this.state.selectedEmployee);
      },
      async handleSave(employee) {
        if(employee.id) {
          await this.$store.dispatch('employee/update', employee);
        } else {
          await this.$store.dispatch('employee/create', employee);
        }
        this.$store.dispatch('employee/load');
        this.handleListSelect(null);
      },
      createNew() {
        this.state.selectedEmployee = new Employee();
      }
    }
}
</script>