<template>
    <div
        class="cell-popup p-15" 
        :class="{[`index-${index}`]: true}"
        :style="{top}"
    >   
        <a href="#" @click.stop="hidePopup" class="close">
            <i class="fa fa-close"></i>
        </a>
        <div class="mb-15">
            <a href="#">
                <i class="fa fa-angle-left"></i>
            </a>
            {{ formated }}
            <a href="#">
                <i class="fa fa-angle-right"></i>
            </a>
        </div>
        <div class='row'>
            <div class="col">
               <customer-autocomplete />
            </div>
        </div>
        <div class='row'>
            <div class="col">
                Избери Услуга: <a href="#service"></a>
            </div>
        </div>
        <div class='row'>
            <div class="col">
                Избери Служител: <a :href="`#employee?filter=service_id=${sid}`"></a>
            </div>
        </div>
        <div class='row'>
            <div class="col">
                Коментар: <a href="#" @click.prevent>Няма</a>
            </div>
        </div>
        <div class="row space-between">
            <div class="col">
                <a href="#" @click.prevent="$emit('cancel')">Откажи</a>
            </div>
            <div class="col">
                <network-button namespace="dialog.create">Запази</network-button>
            </div>
        </div>
    </div>
</template>

<script>
    import { bgformat } from "../../helpers/date";

    export default {
        props: ['date', 'index'],
        data() {
            return {
                top: 0,
                sid: 0
            }
        },
        computed: {
            formated() {
                return this.date;
            }
        },
        mounted() {
            this.top = this.getTop();
            window.addEventListener('resize', event => {
                this.top = this.getTop();
            })
        },
        methods: {
            getTop() {
                const bbox = this.$el.getBoundingClientRect();
                const offset = (window.innerHeight - (bbox.y + bbox.height) - 20);

                return Math.min(0, offset) + 'px';
            },
            hidePopup() {
                this.$store.commit('dialog/hidePopup');
            }
        }
    }
</script>


<style lang="css">
    .cell-popup {
        position: absolute;
        right: 100%;
        margin-right: 15px;
        width: 350px;
        background: #fff;
        box-shadow: 0px 3px 10px rgba(90,90,90, .1);
    }
    .cell-popup.index-0 {
        right: -350px;
        margin-right: -45px;
    }
</style>