import { create, load, update } from "../../services/employees";
import uniq from "lodash/uniq";
import { fromResponse } from "../../components/models/Employee";

function defaultState() {
  return {
    list: [],
    selected: 0,
    services: {},
    loading: false
  }
}

const mutations = {
  load(state, payload) {
    state.list = payload;
  },
  loading(state, payload) {
    state.loading = !!payload;
  },
  select(state, payload) {
    state.selected = payload;
  },
  assignService(state, payload) {
    if(Object.keys(state.services).length === 0) return; // no services assigned to anyone

    const employeeId = payload.employeeId;
    const serviceId = payload.serviceId;

    state.services[employeeId] = uniq([...state.services[employeeId], serviceId])
  },
  removeService(state, payload) {

    if(Object.keys(state.services).length === 0) return; // no services assigned to anyone

    const employeeId = payload.employeeId;
    const serviceId = payload.serviceId;

    state.services[employeeId] = state.services[employeeId].filter(s => s != serviceId);

  },
  setServices(state, list) {
    state.services = list.reduce((memo, employee) => {
      if(!employee.services || employee.services.length == 0) {
        return {
          ...memo,
          [employee.id]: []
        }
      }

      return {
        ...memo,
        [employee.id]: employee.services.map(s => s.id)
      }
    }, {});
  }
}

const actions = {
  async create({ dispatch }, payload) {
    let data = {
      email: payload.email,
      service: payload.service,
      first_name: payload.first_name,
      last_name: payload.last_name,
      phone: payload.phone,
      allow_add: payload.allow_add,
      allow_login: payload.allow_login,
      visible: payload.visible,
    };

    if(payload.allow_login) {
      data.password = payload.password;
      data.password_confirm = payload.password_confirm;
    }

    await create(data);
    dispatch('load');
    dispatch('network/done', 'employee', { root: true });
  },
  async load({ commit, state }, payload) {
    commit("loading", true);
    const includeServices = payload && payload.services;
    const data = await load(payload && payload.id ? payload.id : null, includeServices);

    commit("setServices", data.list);
    commit("loading", false);
    commit("load", fromResponse(data));
  },
  async update({ commit, dispatch, state}, payload) {
    commit("loading", true);
    dispatch('network/load', 'employee', { root: true });

    let data = {
      service: payload.service,
      first_name: payload.first_name,
      last_name: payload.last_name,
      phone: payload.phone,
      allow_add: payload.allow_add,
      visible: payload.visible,
      password: payload.password,
      password_confirm: payload.password_confirm,
      services: !!state.services[payload.id] ? state.services[payload.id] : null
    };

    await update(payload.id, data);
    commit("loading", false);
    dispatch('network/done', 'employee', { root: true });
  }
}

const getters = {
  availableEmployees: state => state.list.filter(e => e.visible && !e.admin && e.unavailable == 0),
  hasAssignedService: state => (employeeId, serviceId) => {

    if(Object.keys(state.services).length = 0) return false;
    if(!state.services[employeeId]) return false;

    return state.services[employeeId].indexOf(serviceId) > -1;
  },
  suggestion: state => state.list.filter(e => e.visible ).map(s => {
    return {
        value: s.id,
        text: `${s.first_name} ${s.last_name}`
    };
  }),
  suggestionWithAppointmentsCount: (state, getters, rootState, rootGetters) => state.list.filter(e => e.visible).map(s => {
    //const appCount = rootGetters['calendar/indexByEmployeeId'][s.id];
    //console.log(rootGetters['calendar/indexByEmployeeId'], s.id,);

    const appointments = rootGetters['calendar/indexByEmployeeId'][s.id];
    return {
      value: s.id,
      text: `${s.first_name} ${s.last_name} (${!!appointments ? appointments.length : 0})`
    };

    // return {
    //   value: s.id,
    //   text: s.name
    // }
  }),
  getById: state => id => {
    const filtered = state.list.filter(e => e.id == id);
    return filtered.length > 0 ? filtered[0] : null;
  }
}

export default {
  namespaced: true,
  actions,
  mutations,
  state: defaultState(),
  getters
};