import { list, create, update, deleteService } from "../../services/services";

function defaultState() {
  return {
    list: []
  }
}

const actions = {
  async create({ commit, dispatch, state }, payload) {
    await create({
      status: payload.status,
      name: payload.name,
      price: payload.price
    });
    
    list().then(resp => {
      commit('loaded', { list: resp.data})
    });
  },
  load({ commit }, payload) {
    list().then(resp => {
      commit('loaded', { list: resp.data });
    });
  },
  async deleteService({ state, commit }, payload) {
    const id = payload.id;
    await deleteService(id);

    return dispatch('load');
  },
  async update({ state, commit }, payload) {
    const { service } = payload;
    
    await update(service.id, {
      name: service.name,
      status: service.status,
      price: service.price
    });
  }
}

const mutations = {
  loaded(state, payload) {
    state.list = payload.list;
  }
}

const getters = {
  getById: state => id => {
    const filtered = state.list.filter(s => s.id == id);
    return filtered.length ? filtered[0] : null
  },
  suggestion: state => state.list.filter(s => s.status === 'available').map(s => {
      return {
          value: s.id,
          text: s.name
      };
  }),
}

export default {
  namespaced: true,
  state: defaultState(),
  mutations,
  actions,
  getters
}