<template>
  <div class="h-100 w-100">
    <div class="top-toolbar p-15 shadow-underline">Настойки за вграждане</div>
    <!-- {{ `${JSON.stringify(embed)}` }} -->
    <div class="p-15">
      <div class="row">
        <div class="col">
          <div>Разрешен домейн</div>
          <div>
            https://
            <text-input v-model="embed.domain"/>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <div>Начин на вграждане</div>
          <div>
            <select v-model="embed.embed_type" class="form-control">
              <option v-for="option in options" :key="option.val" :value="option.val">{{ option.text }}</option>
            </select>
          </div>
        </div>
      </div>
      <template v-if="embed.embed_type == 1 || embed.embed_type == 4">
        <div class="row" v-if="embedCode">
          <div class="col">
            Код за вграждане
            <code class="w-50">{{ embedCode }}</code>
          </div>
        </div>
        <div class="row" v-if="secret">
          <div class="col">
            Код за достъп
            <code class="w-50">{{ secret }}</code>
          </div>
        </div>
        <div class="row" v-if="embed.embed_type == 4">
          <div class="col">
            Token URL
            <text-input v-model="embed.token_url" />
          </div>
        </div>
        <div class="row" v-else>
          <div class="col">
            Инсталирайте <a href="#">нашия Wordpress Plugin</a>
          </div>
        </div>
      </template>
      <template v-if="embed.embed_type == 2">
        <div class="row">
          <div class="col">
            <code>
            {{ '&lt;iframe src="http://some/embed/url"&gt;&lt;/iframe&gt;' }}
          </code>
          </div>
        </div>        
      </template>

      <template v-if="embed.embed_type == 3">
        <div class="row">
          <div class="col">
            <code>
              {{ 'http://some/embed/url' }}
            </code>
          </div>
        </div>
      </template>

      <div class="row">
        <div class="col">Часова зона:</div>
        <div class="col">
          <input-select :suggestions="timezones" :value="embed.tz_offset" @change="handleTimezoneChange" />
        </div>
      </div>

      <div class="row">
        <div class="col" title="Not implemented">* Web Hooks</div>
      </div>
      <div class="row">
        <div class="col">
          Create
          <text-input v-model="embed.create_hook" title="Not implemented" />
        </div>
        <div class="col">
          <div class="col">
            Cancel
            <text-input v-model="embed.cancel_hook" title="Not implemented" />
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col">
          Hook Authorization
          <text-input v-model="embed.hook_authorization" title="Not implemented" />
        </div>
        <div class="col">
          <div class="col">
            Hook Authorization Params
            <text-input v-model="embed.hook_params" title="Not implemented" />
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col">
          <button class="btn" @click="saveDomain">
            Запази
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

const options = [
  { val: 1, text: 'Wordpress' },
  { val: 2, text: 'IFrame' },
  { val: 3, text: 'Link' },
  { val: 4, text: 'Advanced' },
];

const timezones = [];
for(let hour = -12; hour <= 12; hour++ ) {
  timezones.push({
    value: hour * 3600,
    text: (hour < 0 ? "- " : "+ ") + (Math.abs(hour)).toString().padStart(2, "0") + ":00"
  })
}

export default {
  props: {
  //   domain: {
  //     type: [String],
  //     default: ""
  //   },
    embedCode: {
      type: [String],
      default: ""
    },
    secret: {
      type: [String],
      default: ""
    }
  },
  mounted() {
    this.$store.dispatch('embed/load');
  },
  methods: {
    saveDomain() {
      this.$store.dispatch("embed/save");
    },
    handleTimezoneChange(tzvalue) {
      console.log(tzvalue);
      this.$store.commit("embed/fzoffset", {
        tzoffset: tzvalue
      });
    }
  },
  computed: {
    timezones: () => timezones,
    ...mapState('embed', {
      embed: state => state.embed
    }),
    options: () => options
  }
};
</script>
