import { save, load } from "../../services/settings";
import { shortDayOfTheWeek, shortDayNames } from "../../helpers/date";


function defaultState() {
    return {
        workdays: {},
        loading: true
    };
}

function short() {
    return [0,1,2,3,4,5,6].map(shortDayOfTheWeek)
}

const days = short();

const actions = {
    async loadSettings({ commit, state }) {
        const workdays = await load();
        commit('setSettings', { workdays });
        commit('loading', false);
    },
    async saveSettings({ commit, state }, payload) {
        const resp = await save(payload);
    }
}

const mutations = {
    setSettings(state, payload) {
        state.workdays = payload.workdays;
    }
}
const getters = {
    isWorkday: state => date => state.workdays[days[date]] === 1,
    startHour: state => date => state.workdays[`${days[date.getDay()]}_from`],
    endHour: state => date => state.workdays[`${days[date.getDay()]}_to`],
    days: state => shortDayNames.map(day =>  {
        return {
            day,
            workday: state.workdays[day],
            from: state.workdays[day+"_from"],
            to: state.workdays[day+"_to"],
        }
    }),
    workDays: (state, getters) => {
        return getters.days.filter(d => d.workday == 1);
    }, 
    minHour: (state, getters) => {
        const hours = getters.workDays.map( d => parseFloat(d.from.split(':')[0]) );
        return Math.min.apply(Math, [24,...hours]);
    },
    maxHour: (state, getters) => {
        const hours = getters.workDays.map( d => parseFloat(d.to.split(':')[0]) );
        return Math.max.apply(Math, [0,...hours]);
    },
    isWorkhour: state => (date, hour) => {
        const from = state.workdays[`${days[date.getDay()]}_from`].split(':');
        const to = state.workdays[`${days[date.getDay()]}_to`].split(':');

        return hour >= parseFloat(from[0]) && hour < parseFloat(to[0]);
    }
}

export default {
    namespaced: true,
    state: defaultState(),
    getters,
    mutations
}