import { save, load as loadEmbedSettings } from "../../services/embed";
import { load } from "../../services/widgets";

function defaultState() {
  return {
    embed: {
      embed_type: 3,
      token_url: '',
      create_hook: '',
      cancel_hook: '',
      hook_authorization: '',
      hook_params: '',
      domain: '',
      tz_offset: 7200
    },
  }
}

const actions = {
  async load({commit, state}) {
    const rawData = await loadEmbedSettings();
    const data = Object.keys(rawData).reduce((memo, k) => {
      return {
        ...memo,
        [k]: rawData[k] == null ? '' : rawData[k]
      }
    }, defaultState().embed);
    
    commit('load', data)
  },
  async save({ commit, state }) {
    await save(state.embed);
    commit("done");
  }
}

const mutations = {
  load(state, payload) {
    state.embed = payload;
  },
  done(state) {
    state.done = true;
  },
  fzoffset(state, { tzoffset }) {
    state.embed.tz_offset = tzoffset;
  }
}

export default {
  namespaced: true,
  actions,
  mutations,
  state: defaultState()
}