import axios from "axios";

export const acceptJson = { headers: { Accept: 'application/json' } };

export async function load() {
    const { data } = await axios.get('/dashboard/appointments-settings', acceptJson);
    return data.workdays;
}

export async function save(workdays) {
    const { data } = await axios.post('/dashboard/appointments-settings', { workdays }, acceptJson)
    return data;
}