import { save, load } from "../../services/widgets";

function defaultState() {
  return {
    loading: true,
    _server: {},
    settings: {
      bgcolor: '#ffffff',
      fgcolor: '#ffffff',
      text: '#222222',
      position: '',
      icon: ''
    }
  }
}

const actions = {
  async load({ commit, state }) {

    const data = await load();

    if(data.settings) {
      commit('settings', {
        ...defaultState().settings,
        ...JSON.parse(data.settings.params)
      });
  
      commit('saveServer', JSON.parse(data.settings.params));
    }
  },
  
  async save({ commit, state, dispatch }) {
    dispatch('network/load', 'settings', { root: true });
    const { data } = await save({settings: state.settings});
    dispatch('network/done', 'settings', { root: true });
    commit('settings', JSON.parse(data.settings.params));
  }
};

const mutations = {
  settings(state, payload) {
    state.settings = payload;
    state.loading = false;
  },
  saveServer(state, payload) {
    state._server = payload;
  }
};

export default {
  namespaced: true,
  actions,
  mutations,
  state: defaultState()
}