<template>
  <div>
    <div class="top-toolbar">
      Работно време
    </div>
    <div v-for="day in weekDays" :key="day">
      <input type="checkbox" />
      {{ dayOfTheWeek(day) }}
      <div v-for="(wt, index) in workTimeByDays(day)" :key="index">
        <input :value="wt.from" @change="updateFrom($event, wt)" />
        <input :value="wt.to" @change="updateTo($event, wt)" />
        <span class="fa fa-trash" @click="removeWorktime(wt.id)"></span>
      </div>
      <button @click="addWorktime(day)">Добави работен ден</button>
    </div>
    <save-button @save="save" pipe="settings">Запази</save-button>
  </div>
</template>

<script>
import { mapGetters } from "axios";
import { dayOfTheWeek } from "../../helpers/date";
import { debounce as t } from "lodash";

export default {
  created() {
    this.$store.dispatch('worktime/load');
  },
  computed: {
    weekDays() {
      return this.$store.state['worktime'].days;
    },
    workTimeByDays() {
      return this.$store.getters['worktime/dayWorktime'];
    }
  },
  methods: {
    dayOfTheWeek,
    addWorktime(day) {
      this.$store.commit('worktime/addWorktime', { day });
    },
    save() {
      this.$store.dispatch('worktime/save');
    },
    update(value, worktime, field) {
      const change = {
        ...worktime,
        [field]: value
      };
      this.$store.commit('worktime/update', { original: worktime, change });
    },
    updateFrom(event, worktime) {
      this.update(event.target.value, worktime, 'from');
    },
    updateTo(event, worktime) {
      this.update(event.target.value, worktime, 'to');
    },
    removeWorktime(id) {
      this.$store.commit('worktime/remove', { id });
    }
  }
}
</script>
