import { load, create, update } from "../../services/customers";

function defaultState() {
    return {
        list: []
    }
}

const actions = {
    async load({ commit}) {
        const customers = await load();
        
        commit("load", { customers });
    },

    async create({ commit, dispatch }, { customer }) {
        await create(customer);
        dispatch("load");
    },

    async update({ commit }, { customer }) {
        await update(customer);
    }
}

const getters = {
    filter: state => filter => {
        if(!filter && filter.length < 3) return state.list;

        const list = state.list.filter(c => {
            const match = 
                c.name.indexOf(filter) > -1 || 
                c.email.indexOf(filter) > -1 ||
                c.phone.indexOf(filter) > -1;

            console.log(match, c);
            return match;
        });
        return list;
    }
}

const mutations = {
    load(state, { customers }) {
        state.list = customers;
    }
}

export default {
    state: defaultState(),
    actions,
    mutations,
    getters,
    namespaced: true
}